.info-product-container{max-width:970px;margin:0 auto 30px auto}.info-product-container .single-info{display:flex;flex-direction:column;cursor:pointer}.info-product-container .single-info p{margin:15px 0 15px 55px;font-size:14px;font-family:"Arial";color:#fff;text-transform:uppercase;text-align:left;position:relative;appearance:none}.info-product-container .single-info p::before{content:" ";border:1px solid #fff;background-color:rgba(255,255,255,.2);position:absolute;left:-40px;top:-7px;width:26px;height:26px;appearance:none}.info-product-container .single-info p::after{position:absolute;font-family:"Font Awesome 5 Free";font-weight:900;color:#fff;content:"";left:-29px;top:0;appearance:none}.info-product-container .single-info .text-info{display:none}.info-product-container .single-info .text-info.expanded{display:block;text-align:left;margin:0 15px 15px 15px;color:#fff;font-size:12px}.info-product-container .single-info:first-child{background-color:#ffcab8}.info-product-container .single-info:last-child{background-color:#ffa687}@media(max-width: 768px){.info-product-container{max-width:90%}}